#Profile {
  background-size: cover;
  background-repeat: no-repeat;
  background-position: bottom center;
  /* height: 720px; */
  border: 1px solid rgba(0, 0, 0, 0.15);
  /* background-color: #3d3d3d; */
  background-color: #273445;
  /* background-color: #6f8d3e; */
  color: #fff;
  padding-top: 35px;
  padding-bottom: 35px;
}

#Profile table tr th {
  padding-right: 0.8rem;
  text-align: right;
}

#Profile .title {
  color: #fff;
}

#Profile .img-frame {
  overflow: hidden;
  /* display: inline-block; */
  /* z-index: 1; */
  /* position: relative; */

  /* margin-top: -100px; */
  margin-bottom: 20px;
}

#Profile img.dp {
  /* height: 135px; */
  height: 125px;
  width: 125px;
  vertical-align: middle;
  border: 1px solid rgba(0, 0, 0, 0.15);
  box-sizing: border-box;
  box-shadow: inset 0 0 0 1px rgba(255, 255, 255, 0.05),
    inset 0 1px 0 0 rgba(255, 255, 255, 0.2),
    inset 0 -1px 0 0 rgba(255, 255, 255, 0.1), 0 0px 5px rgba(0, 0, 0, 0.7);
  background-color: #959595;
  border-radius: 100%;
}

@media (max-width: 350px) {
  #Profile img.dp {
    height: 75px;
    width: 75px;
  }

  #About h2 {
    font-size: 1.1rem;
  }
}

.dp.zoom-1 {
  transform: scale(1.25);
}

#Profile h1 small {
  font-weight: 400;
  font-size: 1.2rem;
  display: block;
}

/* #Profile h1 {
	color: #fff;
	text-shadow: 2px 2px 3px #3d3d3d;
} */

#Profile .card {
  /* background-color: rgba(255, 255, 255, 0.9); */
  background-color: rgba(255, 255, 255, 0.85);
  /* margin-bottom: 50px; */
}

.card-body p {
  line-height: 2.2rem;
}

#Profile .divider {
  height: 25px;
}

#About {
  margin-bottom: 10px;
  display: flex;
  flex-direction: row;
  align-items: flex-start;
}

#About .img-frame {
  flex-shrink: 0;
}

#About .about-caption {
  padding-left: 1rem;
}

#About h2 {
  padding: 0;
  margin: 0;
  /* margin-top: 10px; */
}
