html,
body {
  height: 100%;
}

html {
  scroll-behavior: smooth;
}

body {
  padding: 0px;
  /* padding-top: 70px; */
  margin: 0;
  font-family: "Sulphur Point", "Open Sans", sans-serif;
  background-color: #fff;
  color: #555555;
  font-size: 120%;
}

a {
  text-decoration: none;
}

table,
td,
th {
  vertical-align: top;
}

/* Common Class */
.primary-text {
  color: #83a036 !important;
}

.secondary-text {
  color: #808080;
}

.title {
  text-align: center;
  font-size: 2.5rem;
}

.btn {
  color: #fff;
  display: inline-block;
  font-weight: 400;
  font-size: 1rem;
  padding: 10px 15px;
  border-radius: 1rem;
  text-shadow: 0px 1px 1px rgba(0, 0, 0, 0.25);
  border: 1px solid rgba(0, 0, 0, 0.15);
  border-radius: 0.25rem;
  box-shadow: inset 0 0 0 1px rgba(255, 255, 255, 0.05),
    inset 0 1px 0 0 rgba(255, 255, 255, 0.2),
    inset 0 -1px 0 0 rgba(255, 255, 255, 0.1),
    inset 0 -1px 0 0 rgba(255, 255, 255, 0.1), 0 1px 3px rgba(0, 0, 0, 0.3);
  cursor: pointer;
}

.btn.btn-sm {
  font-size: 0.8rem;
  padding: 5px 10px;
}

.btn:active {
  border: 1px solid rgba(0, 0, 0, 0.9);
  box-shadow: inset 0 0 0 1px rgba(0, 0, 0, 0.05),
    inset 0 1px 0 0 rgba(0, 0, 0, 0.2), inset 0 -1px 0 0 rgba(0, 0, 0, 0.1),
    inset 0 -1px 0 0 rgba(0, 0, 0, 0.1), 0 1px 3px rgba(0, 0, 0, 0.3);
}

/* .btn:hover {
	border: 1px solid rgba(0, 0, 0, 0.6);
} */

.btn-block {
  display: block;
  width: 100%;
  box-sizing: border-box;
}

.btn-primary {
  background-color: #83a036;
}

.btn-primary:hover {
  background-color: #617528;
}

.btn-info {
  background-color: #3794ca;
}

.btn-danger {
  background-color: #ca3737;
}

.btn-danger:hover {
  background-color: #9e2d2d;
}

.btn-warning {
  background-color: #bb9928;
}

.card {
  background-color: #fff;
  box-shadow: 0px 0px 15px #3d3d3d;
  border-radius: 0.25rem;
  color: #555;
  background-color: var(--silver-100);
  background-origin: border-box;
  background-position: 50%;
  background-size: cover;
  border: 1px solid rgba(0, 0, 0, 0.15);
  border-radius: 0.35rem;
  box-shadow: inset 0 0 0 1px rgba(255, 255, 255, 0.05),
    inset 0 1px 0 0 rgba(255, 255, 255, 0.2),
    inset 0 -1px 0 0 rgba(255, 255, 255, 0.1),
    inset 0 -1px 0 0 rgba(255, 255, 255, 0.1), 0 1px 3px rgba(0, 0, 0, 0.7);
  padding: 1.2rem;
}

.card h2 {
  border-left: 5px solid #83a036;
  padding-left: 10px;
  padding-top: 0;
  padding-bottom: 0;
  margin: 0 !important;
}

.end-xs .card h2,
.end-sm .card h2,
.end-md .card h2,
.end-lg .card h2 {
  border-left: none;
  padding-left: 0;
  border-right: 5px solid #83a036;
  padding-right: 10px;
}

.card .card-header {
  padding-top: 5px;
  padding-bottom: 5px;
}

.card .card-footer {
  border-top: 1px solid rgba(0, 0, 0, 0.15);
  padding-top: 10px;
}

.container {
  /* width: 100%; */
  padding-right: 15px !important;
  padding-left: 15px !important;
  /* margin-right: auto; */
  /* margin-left: auto; */
}

@media (min-width: 576px) {
  .container {
    max-width: 540px;
  }
}

@media (min-width: 768px) {
  .container {
    max-width: 720px;
  }
}

@media (min-width: 992px) {
  .container {
    max-width: 960px;
  }
}

@media (min-width: 1200px) {
  .container {
    max-width: 1140px;
  }
}
